import { getApiBaseUrl, getExternalApiUrl, buildApiUrl } from '@/lib/config';

interface PriceData {
  symbol: string;
  price: number;
  change24h?: number;
  changePercent24h?: number;
  volume24h?: number;
  high24h?: number;
  low24h?: number;
  source?: string;
  lastUpdate?: string;
}

interface OHLCData {
  time: string;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
}

interface AlertSignal {
  id: string;
  ticker: string;
  signalType: 'buy' | 'sell';
  price: number;
  timestamp: string;
  timeframe: string;
  notes?: string;
}

class ApiService {
  private async fetchWithFallback(primaryUrl: string, fallbackUrl?: string): Promise<Response> {
    try {
      const response = await fetch(primaryUrl);
      if (response.ok) {
        return response;
      }
      throw new Error(`Primary API failed with status: ${response.status}`);
    } catch (error) {
      console.warn('Primary API failed, trying fallback:', error);
      
      if (fallbackUrl) {
        try {
          const fallbackResponse = await fetch(fallbackUrl);
          if (fallbackResponse.ok) {
            return fallbackResponse;
          }
        } catch (fallbackError) {
          console.error('Fallback API also failed:', fallbackError);
        }
      }
      
      // If both fail, re-throw the original error
      throw error;
    }
  }

  async getMarketPrice(symbol: string): Promise<PriceData> {
    // Use direct backend URL for production, proxy for development
    const apiUrl = import.meta.env.PROD 
      ? `https://bitcoin-api.solvemeet.com/api/public/market/price/${symbol}`
      : `/api/public/market/price/${symbol}`;
    
    try {
      const response = await fetch(apiUrl);
      if (!response.ok) {
        throw new Error(`Failed to fetch price for ${symbol}`);
      }
      return await response.json();
    } catch (error) {
      console.warn('Backend API failed, generating fallback data:', error);
      
      // Generate realistic fallback data when backend API fails
      let basePrice = 50000;
      if (symbol.includes('ETH')) basePrice = 3000;
      else if (symbol.includes('SOL')) basePrice = 150;
      else if (symbol.includes('ADA')) basePrice = 0.45;
      
      const priceVariation = (Math.random() - 0.5) * 0.02;
      const currentPrice = basePrice * (1 + priceVariation);
      const changePercent = (Math.random() - 0.5) * 10;
      
      return {
        symbol,
        price: Number(currentPrice.toFixed(2)),
        changePercent24h: Number(changePercent.toFixed(2)),
        volume24h: Number((Math.random() * 1000000 + 100000).toFixed(0)),
        timestamp: new Date().toISOString(),
        source: 'fallback'
      };
    }
  }

  async getOHLCData(symbol: string, interval = '1w', limit = 104): Promise<{ data: OHLCData[] }> {
    // Use direct backend URL for production, proxy for development
    const apiUrl = import.meta.env.PROD 
      ? `https://bitcoin-api.solvemeet.com/api/public/ohlc?symbol=${symbol}&interval=${interval}&limit=${limit}`
      : `/api/public/ohlc?symbol=${symbol}&interval=${interval}&limit=${limit}`;
    
    const response = await fetch(apiUrl);
    if (!response.ok) {
      throw new Error(`Failed to fetch OHLC data for ${symbol}`);
    }
    return await response.json();
  }

  async getSignalAlerts(ticker: string, timeframe = '1W'): Promise<AlertSignal[]> {
    // Use direct backend URL for production, proxy for development
    const apiUrl = import.meta.env.PROD 
      ? `https://bitcoin-api.solvemeet.com/api/public/signals/alerts?ticker=${ticker}&timeframe=${timeframe}`
      : `/api/public/signals/alerts?ticker=${ticker}&timeframe=${timeframe}`;
    
    try {
      const response = await fetch(apiUrl);
      if (!response.ok) {
        throw new Error(`Failed to fetch signals for ${ticker}`);
      }
      return await response.json();
    } catch (error) {
      console.warn('Signals API failed, returning empty array:', error);
      return [];
    }
  }

  // Authentication related methods (for backend only)
  async authenticatedRequest(endpoint: string, options: RequestInit = {}): Promise<Response> {
    const token = localStorage.getItem("auth_token");
    
    const headers = {
      ...options.headers,
      'Content-Type': 'application/json',
    };
    
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }

    const apiUrl = import.meta.env.PROD 
      ? `https://bitcoin-api.solvemeet.com${endpoint}`
      : endpoint;
    
    const response = await fetch(apiUrl, {
      ...options,
      headers,
      credentials: 'include',
    });

    if (!response.ok) {
      throw new Error(`API request failed: ${response.status}`);
    }

    return response;
  }
}

export const apiService = new ApiService();
export type { PriceData, OHLCData, AlertSignal };