// API Configuration for different environments
export const API_CONFIG = {
  // Use environment variable or fallback to live backend URL
  BASE_URL: (import.meta as any).env?.VITE_API_URL || 'https://bitcoin-api.solvemeet.com',
  
  // Backend service URL (your deployed backend)
  BACKEND_URL: (import.meta as any).env?.VITE_BACKEND_URL || 'https://bitcoin-api.solvemeet.com',
  
  // External API for price data
  EXTERNAL_API: (import.meta as any).env?.VITE_API_URL || 'https://bitcoin-api.solvemeet.com',
  
  // Timeout settings
  TIMEOUT: 10000, // 10 seconds
  
  // Retry configuration
  RETRY_ATTEMPTS: 3,
  RETRY_DELAY: 1000, // 1 second
};

// Helper function to determine if we're in development
export const isDevelopment = () => {
  return (import.meta as any).env?.DEV || window.location.hostname === 'localhost';
};

// Get the correct API base URL based on environment
export const getApiBaseUrl = () => {
  // Always use the backend URL for API calls
  return API_CONFIG.BACKEND_URL;
};

// Get external API URL for direct price data access
export const getExternalApiUrl = () => {
  return API_CONFIG.EXTERNAL_API;
};

// Helper to build API URLs
export const buildApiUrl = (endpoint: string) => {
  const baseUrl = getApiBaseUrl();
  return baseUrl ? `${baseUrl}${endpoint}` : endpoint;
};